import React, { useEffect, useRef } from 'react';

const AnimatedComponent = () => {
  // Explicitly define the type of elementRef as a reference to a div element
  const elementRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    // Add the animation class when the component mounts
    if (elementRef.current) {
      elementRef.current.classList.add('animate-fade-in');
    }
  }, []);

  return (
    <div
      ref={elementRef}
      className="opacity-0" // Start with opacity 0 (hidden)
    >
      <h1 className="text-4xl font-bold text-pink-400">Hello, World!</h1>
      <p className="text-lg text-pink-400">This element fades in on load.</p>
    </div>
  );
};

export default AnimatedComponent;